.list{
    overflow-x: hidden;
    overflow-y: auto;
    height: 230px;
}

.list::-webkit-scrollbar {
    width: 7px;
}
.list::-webkit-scrollbar-track {
        box-shadow: inset 0 0 5px grey; 
        border-radius: 10px;
}
.list::-webkit-scrollbar-thumb {
    background: grey; 
    border-radius: 10px;
}